@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700;800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "poppins", Arial, Helvetica, sans-serif;
  font-size: 0.7vw;
}

.modalOverlay {
  background-color: rgba(0, 0, 0, 0.7);
  position: absolute;
  height: 100vh;
  width: 100vw;
  z-index: 100;
}
.container {
  height: 100vh;
  overflow: hidden;
  position: fixed;
  width: 100vw;
  background-color: #ecf0f1;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 0.05fr;
  grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-gap: 20px 0px;
  gap: 20px 0px;
  grid-auto-flow: row;
  grid-template-areas:
    "Pie Header Header"
    "Pie Header Header"
    "Pie Time-Selector Time-Selector"
    "Pie General-Info General-Info"
    "Pie General-Info General-Info"
    "Pie General-Info General-Info"
    "Pie General-Info General-Info"
    "Pie General-Info General-Info"
    "Devices Graph Graph"
    "Devices Graph Graph"
    "Devices Graph Graph"
    "Devices Graph Graph"
    "Devices Graph Graph"
    "Devices Graph Graph"
    "Devices Graph Graph"
    "Devices Graph Graph"
    "Devices Graph Graph"
    "Devices Graph Graph"
    "Footer Footer Footer";
}

.Pie {
  grid-area: Pie;
  margin-top: 20px;
}

.Header {
  grid-area: Header;
  background-color: #52af3f;
}

.Time-Selector {
  grid-area: Time-Selector;
  display: flex;
  justify-content: center;
}

.General-Info {
  grid-area: General-Info;
  background: #474747;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.09);
  border-radius: 18px;
  margin-bottom: 10px;
}

.Graph {
  grid-area: Graph;
  margin-bottom: 10px;
}

.Devices {
  margin-top: 20px;
  grid-area: Devices;
  z-index: 10;
  overflow-y: scroll;
  /* display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px; */
}

.Postal-Info {
  grid-area: Postal-Info;
  background: #c22626;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.09);
  border-radius: 18px;
}

.Footer {
  grid-area: Footer;
}

.CurrentNameHeader {
  text-align: center;
  font-size: 2rem;
}

.powericon {
  box-sizing: content-box;
  font-size: 22px;
  margin-right: 10px;
  color: white;
  padding: 8px;
  border-radius: 50%;
  transition: opacity 0.2s;
}

.powericon:hover {
  opacity: 0.6;
}

.settingsmodalhome {
  position: absolute;
  z-index: 200;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.graph {
  height: 100%;
  width: 100%;
  background: #f6f6f6;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.09);
  border-radius: 18px;
  padding-bottom: 40px;
  padding-left: 20px;
}

.piegraph {
  width: 100%;
  height: 100%;
  position: relative;
}
.overlay {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  pointer-events: none;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  grid-gap: 10px;
  gap: 10px;
  z-index: 1;
}

.piechart {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  transform: scale(120%) translateY(4%);
  z-index: 2;
}

.powerusage {
  font-size: 1.25rem;
  font-weight: 400;
}

.wattamount {
  font-weight: 800;
  font-size: 2.5rem;
  transform: translateY(5px);
}

.watts {
  font-weight: 600;
  font-size: 1.6rem;
  letter-spacing: 3%;
}

.logo {
  height: 100%;
  width: auto;
  margin-left: 15%;
}
.dashboardText {
  font-size: 2.4rem;
  color: white;
  margin-left: 3%;
  white-space: nowrap;
}
.scanIcon {
  color: white;
  height: 80%;
  width: inherit;
}
.settings {
  background-color: #ffffff;
  height: 50%;
  padding-left: 10px;
  padding-right: 10px;
  border-radius: 75px;
  margin-left: 2%;
  display: flex;
  flex-direction: row;
  align-items: center;
  grid-gap: 5%;
  gap: 5%;
  justify-content: center;
  border: none;
  cursor: pointer;
}
.settingIcon {
  color: #316526;
  height: 80%;
  width: inherit;
}
.logOut {
  background-color: #d35400;
  height: 50%;
  padding-left: 10px;
  padding-right: 10px;
  border-radius: 75px;
  margin-left: 2%;
  display: flex;
  flex-direction: row;
  align-items: center;
  grid-gap: 5%;
  gap: 5%;
  justify-content: center;
  border: none;
  cursor: pointer;
}
.logOutIcon {
  color: white;
  height: 80%;
  width: inherit;
}

.buttons {
  display: flex;
  margin-right: 40px;
  height: 100%;
  align-items: center;
  justify-content: center;
  grid-gap: 20px;
  gap: 20px;
}

.range-bar {
  width: 100%;
  height: 100%;
}

.general-box {
  width: 100%;
  height: 100%;
  background: #f6f6f6;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.09);
  border-radius: 18px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.kosten-test {
  margin-left: 20px;
  margin-top: 30px;
  font-size: 1.5rem;
}

.range-bar-box {
  height: 50%;
  width: 100%;
}

.text-items {
  display: flex;
  grid-gap: 100px;
  gap: 100px;
}

.background {
  background-color: #ecf0f1;
  justify-content: center;
  display: flex;
  flex-direction: column;
  padding-top: 3%;
  padding-left: 10%;
  padding-bottom: 7%;
  width: 100%;
  height: 100%;
}
.bar1 {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.head {
  font-size: 32px;
  margin-top: 5%;
  white-space: nowrap;
}
.close {
  height: 40px;
  width: 40px;
  margin-right: 20px;
  cursor: pointer;
}
.bar2 {
  display: flex;
  flex-direction: row;
  margin-top: 5%;
  grid-gap: 1%;
  gap: 1%;
}
.text {
  font-size: 24px;
}
.bar3 {
  justify-content: center;
  align-items: center;
}
.inputEmail {
  display: flex;
  width: 85%;
  border-width: 2px;
  font-size: 26px;
  font-weight: 300;
}
.bar4 {
  display: flex;
  flex-direction: row;
  margin-top: 4%;
}
.postal,
.energy {
  display: flex;
  font-size: 28px;
  grid-gap: 1%;
  gap: 1%;
  flex: 1 1;
}
.icon {
  width: 28px;
  height: 28px;
  margin-top: 2%;
}
.bar5 {
  display: flex;
  flex-direction: row;
}
.bar5div {
  display: flex;
  flex: 1 1;
}
.bar5input {
  border-width: 2px;
  font-size: 26px;
  font-weight: 300;
  width: 70%;
}
.savebutton {
  background-color: #52af3f;
  color: white;
  width: 86.4%;
  margin-top: 5%;
  border-radius: 5px;
  font-size: 37px;
  border-width: 0px;
  cursor: pointer;
}
.changepassword {
  background-color: #52af3f;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 86.4%;
  border-radius: 5px;
  margin-top: 15%;
  font-size: 24px;
  grid-gap: 2%;
  gap: 2%;
  padding: 1%;
  border-width: 0px;
  cursor: pointer;
}
.deleteaccount {
  background-color: red;
  margin-top: 2%;
  width: 86.4%;
  border-radius: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  font-size: 24px;
  grid-gap: 2%;
  gap: 2%;
  padding: 1%;
  border-width: 0px;
  cursor: pointer;
}
.changepassword:hover,
.savebutton:hover {
  background-color: #4ca33b;
}
.deleteaccount:hover {
  background-color: #e00;
}
.close:hover {
  opacity: 0.75;
}

.device-card {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 10px;
  border-radius: 18px;
  position: relative;
  overflow: hidden;
  width: 85%;
  margin: 10px 10px;
  height: 80px;
  margin: auto;
  margin-bottom: 20px;
  margin-top: 10px;
}

.block {
  height: 100%;
  width: 30px;
  transform: scale(300%);
  margin-right: 40px;
}

.device-button {
  background: none;
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* .power-icon {
  font-size: 2rem;
  margin-left: 10px;
  margin-right: 40px;
} */

.device-name {
  font-weight: 600;
  font-size: 1.3rem;
  max-width: 220px;
  margin-left: 20px;
}

.power-card {
  padding: 5px 10px;
  border-radius: 26px;
  margin-left: auto;
}

.power-data-label {
  color: #ffffff;
  font-size: 1.5rem;
  font-weight: 600;
}

