.logo {
  height: 100%;
  width: auto;
  margin-left: 15%;
}
.dashboardText {
  font-size: 2.4rem;
  color: white;
  margin-left: 3%;
  white-space: nowrap;
}
.scanIcon {
  color: white;
  height: 80%;
  width: inherit;
}
.settings {
  background-color: #ffffff;
  height: 50%;
  padding-left: 10px;
  padding-right: 10px;
  border-radius: 75px;
  margin-left: 2%;
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 5%;
  justify-content: center;
  border: none;
  cursor: pointer;
}
.settingIcon {
  color: #316526;
  height: 80%;
  width: inherit;
}
.logOut {
  background-color: #d35400;
  height: 50%;
  padding-left: 10px;
  padding-right: 10px;
  border-radius: 75px;
  margin-left: 2%;
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 5%;
  justify-content: center;
  border: none;
  cursor: pointer;
}
.logOutIcon {
  color: white;
  height: 80%;
  width: inherit;
}

.buttons {
  display: flex;
  margin-right: 40px;
  height: 100%;
  align-items: center;
  justify-content: center;
  gap: 20px;
}
