.background {
  background-color: #ecf0f1;
  justify-content: center;
  display: flex;
  flex-direction: column;
  padding-top: 3%;
  padding-left: 10%;
  padding-bottom: 7%;
  width: 100%;
  height: 100%;
}
.bar1 {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.head {
  font-size: 32px;
  margin-top: 5%;
  white-space: nowrap;
}
.close {
  height: 40px;
  width: 40px;
  margin-right: 20px;
  cursor: pointer;
}
.bar2 {
  display: flex;
  flex-direction: row;
  margin-top: 5%;
  gap: 1%;
}
.text {
  font-size: 24px;
}
.bar3 {
  justify-content: center;
  align-items: center;
}
.inputEmail {
  display: flex;
  width: 85%;
  border-width: 2px;
  font-size: 26px;
  font-weight: 300;
}
.bar4 {
  display: flex;
  flex-direction: row;
  margin-top: 4%;
}
.postal,
.energy {
  display: flex;
  font-size: 28px;
  gap: 1%;
  flex: 1;
}
.icon {
  width: 28px;
  height: 28px;
  margin-top: 2%;
}
.bar5 {
  display: flex;
  flex-direction: row;
}
.bar5div {
  display: flex;
  flex: 1;
}
.bar5input {
  border-width: 2px;
  font-size: 26px;
  font-weight: 300;
  width: 70%;
}
.savebutton {
  background-color: #52af3f;
  color: white;
  width: 86.4%;
  margin-top: 5%;
  border-radius: 5px;
  font-size: 37px;
  border-width: 0px;
  cursor: pointer;
}
.changepassword {
  background-color: #52af3f;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 86.4%;
  border-radius: 5px;
  margin-top: 15%;
  font-size: 24px;
  gap: 2%;
  padding: 1%;
  border-width: 0px;
  cursor: pointer;
}
.deleteaccount {
  background-color: red;
  margin-top: 2%;
  width: 86.4%;
  border-radius: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  font-size: 24px;
  gap: 2%;
  padding: 1%;
  border-width: 0px;
  cursor: pointer;
}
.changepassword:hover,
.savebutton:hover {
  background-color: #4ca33b;
}
.deleteaccount:hover {
  background-color: #e00;
}
.close:hover {
  opacity: 0.75;
}
