@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700;800&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "poppins", Arial, Helvetica, sans-serif;
  font-size: 0.7vw;
}

.modalOverlay {
  background-color: rgba(0, 0, 0, 0.7);
  position: absolute;
  height: 100vh;
  width: 100vw;
  z-index: 100;
}
.container {
  height: 100vh;
  overflow: hidden;
  position: fixed;
  width: 100vw;
  background-color: #ecf0f1;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 0.05fr;
  grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  gap: 20px 0px;
  grid-auto-flow: row;
  grid-template-areas:
    "Pie Header Header"
    "Pie Header Header"
    "Pie Time-Selector Time-Selector"
    "Pie General-Info General-Info"
    "Pie General-Info General-Info"
    "Pie General-Info General-Info"
    "Pie General-Info General-Info"
    "Pie General-Info General-Info"
    "Devices Graph Graph"
    "Devices Graph Graph"
    "Devices Graph Graph"
    "Devices Graph Graph"
    "Devices Graph Graph"
    "Devices Graph Graph"
    "Devices Graph Graph"
    "Devices Graph Graph"
    "Devices Graph Graph"
    "Devices Graph Graph"
    "Footer Footer Footer";
}

.Pie {
  grid-area: Pie;
  margin-top: 20px;
}

.Header {
  grid-area: Header;
  background-color: #52af3f;
}

.Time-Selector {
  grid-area: Time-Selector;
  display: flex;
  justify-content: center;
}

.General-Info {
  grid-area: General-Info;
  background: #474747;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.09);
  border-radius: 18px;
  margin-bottom: 10px;
}

.Graph {
  grid-area: Graph;
  margin-bottom: 10px;
}

.Devices {
  margin-top: 20px;
  grid-area: Devices;
  z-index: 10;
  overflow-y: scroll;
  /* display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px; */
}

.Postal-Info {
  grid-area: Postal-Info;
  background: #c22626;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.09);
  border-radius: 18px;
}

.Footer {
  grid-area: Footer;
}

.CurrentNameHeader {
  text-align: center;
  font-size: 2rem;
}

.powericon {
  box-sizing: content-box;
  font-size: 22px;
  margin-right: 10px;
  color: white;
  padding: 8px;
  border-radius: 50%;
  transition: opacity 0.2s;
}

.powericon:hover {
  opacity: 0.6;
}

.settingsmodalhome {
  position: absolute;
  z-index: 200;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
